'use client';

import React from 'react';
import { useRouter, usePathname } from 'next/navigation';
import { useNotifications } from '../hooks/useNotifications';
import { useNavItems } from '../hooks/useNavItems';

const BottomNav: React.FC = () => {
  const router = useRouter();
  const pathname = usePathname();
  const { unreadCount } = useNotifications();
  const { visibleNavItems } = useNavItems();

  const isActive = (path: string) => {
    if (path === '/matches' && pathname.includes('/match/')) return true;
    return pathname.includes(path);
  };

  return (
    <div className="fixed bottom-0 left-1/2 -translate-x-1/2 w-auto min-w-[320px] bg-white/95 backdrop-blur-xl border-t border-x border-white/40 shadow-[0_-5px_15px_-5px_rgba(0,0,0,0.1)] rounded-t-[24px] flex items-center justify-center gap-8 px-6 py-2 z-50 landscape:hidden">
      {visibleNavItems.map((item) => (
        <button
          key={item.path}
          onClick={() => router.push(item.path)}
          className={`group relative flex flex-col items-center justify-center p-2 transition-all ${isActive(item.path) ? 'text-primary' : 'text-slate-400 hover:text-slate-600'
            }`}
        >
          <div className={`relative transition-all duration-300 ${isActive(item.path) ? '-translate-y-1' : 'group-hover:-translate-y-1'}`}>
            <span className={`material-symbols-outlined text-2xl transition-transform ${isActive(item.path) ? 'icon-filled scale-110' : ''}`}>
              {item.icon}
            </span>
            {item.showBadge && unreadCount > 0 && (
              <span className="absolute -top-1 -right-1 flex h-3.5 min-w-[14px] px-1 items-center justify-center rounded-full bg-red-500 text-[9px] font-bold text-white border-2 border-white">
                {unreadCount > 9 ? '9+' : unreadCount}
              </span>
            )}
          </div>

          {/* Tooltip Title */}
          <span className="absolute -top-10 opacity-0 group-hover:opacity-100 transition-opacity bg-slate-800 text-white text-[10px] font-bold py-1 px-2 rounded-lg shadow-lg pointer-events-none whitespace-nowrap">
            {item.name}
            {/* Tiny arrow */}
            <span className="absolute bottom-[-4px] left-1/2 -translate-x-1/2 border-l-4 border-r-4 border-t-4 border-transparent border-t-slate-800"></span>
          </span>
        </button>
      ))}
    </div>
  );
};

export default BottomNav;